import {createElement} from '../render.js';

function createTripItemTemplate() {
  return (
    '<li class="trip-events__item">\n' +
    '              <div class="event">\n' +
    '                <time class="event__date" datetime="2019-03-18">MAR 18</time>\n' +
    '                <div class="event__type">\n' +
    '                  <img class="event__type-icon" width="42" height="42" src="img/icons/flight.png" alt="Event type icon">\n' +
    '                </div>\n' +
    '                <h3 class="event__title">Flight Chamonix</h3>\n' +
    '                <div class="event__schedule">\n' +
    '                  <p class="event__time">\n' +
    '                    <time class="event__start-time" datetime="2019-03-18T12:25">12:25</time>\n' +
    '                    —\n' +
    '                    <time class="event__end-time" datetime="2019-03-18T13:35">13:35</time>\n' +
    '                  </p>\n' +
    '                  <p class="event__duration">01H 10M</p>\n' +
    '                </div>\n' +
    '                <p class="event__price">\n' +
    '                  €&nbsp;<span class="event__price-value">160</span>\n' +
    '                </p>\n' +
    '                <h4 class="visually-hidden">Offers:</h4>\n' +
    '                <ul class="event__selected-offers">\n' +
    '                  <li class="event__offer">\n' +
    '                    <span class="event__offer-title">Add luggage</span>\n' +
    '                    +€&nbsp;\n' +
    '                    <span class="event__offer-price">50</span>\n' +
    '                  </li>\n' +
    '                  <li class="event__offer">\n' +
    '                    <span class="event__offer-title">Switch to comfort</span>\n' +
    '                    +€&nbsp;\n' +
    '                    <span class="event__offer-price">80</span>\n' +
    '                  </li>\n' +
    '                </ul>\n' +
    '                <button class="event__favorite-btn" type="button">\n' +
    '                  <span class="visually-hidden">Add to favorite</span>\n' +
    '                  <svg class="event__favorite-icon" width="28" height="28" viewBox="0 0 28 28">\n' +
    '                    <path d="M14 21l-8.22899 4.3262 1.57159-9.1631L.685209 9.67376 9.8855 8.33688 14 0l4.1145 8.33688 9.2003 1.33688-6.6574 6.48934 1.5716 9.1631L14 21z"></path>\n' +
    '                  </svg>\n' +
    '                </button>\n' +
    '                <button class="event__rollup-btn" type="button">\n' +
    '                  <span class="visually-hidden">Open event</span>\n' +
    '                </button>\n' +
    '              </div>\n' +
    '            </li>'
  );
}

export default class NewTripItem {
  getTemplate() {
    return createTripItemTemplate();
  }

  getElement() {
    if (!this.element) {
      this.element = createElement(this.getTemplate());
    }

    return this.element;
  }

  removeElement() {
    this.element = null;
  }
}
